.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.gallery {
  max-width: 1000px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  box-sizing: border-box;
  background-color: #f5f5f5;
  border-radius: 5px;
  flex-basis: calc(20% - 20px);
  margin-bottom: 20px;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.info {
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.info-item {
  color: #333;
  margin-right: 20px;
  font-size: 14px;
}

